import { Component, h, Host, Element } from '@stencil/core';

@Component({
	tag: 'o-card',
	styleUrl: 'o-card.scss',
	shadow: true
})
export class OCard {
	@Element() el: HTMLElement;

	/** Checks if the header slot has content */
	private hasHeaderSlot(): boolean {
		return !!this.el.querySelector('[slot="header-text"], [slot="header-icon"]');
	}

	render() {
		return (
			<Host>
				<div class="w-full bg-background-default rounded-radius-lg flex flex-col">
					{this.hasHeaderSlot() && (
						<div class="flex justify-between items-center px-3 py-2 border-b-width-sm border-border-default title-default">
							<slot name="header-text"></slot>
							<slot name="header-icon"></slot>
						</div>
					)}
					<div class="p-4 w-full">
						<slot></slot>
					</div>
				</div>
			</Host>
		);
	}
}
